@charset "UTF-8";
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html {
  font-size: 62.5%;
  scroll-behavior: smooth;
}

body,
html {
  height: 100%;
  scroll-padding-top: 80px;
}

body {
  font-family: "Montserrat", sans-serif;
  background-color: #fff;
  overflow-x: hidden;
  width: 100%;
}

.container {
  margin: 0 auto;
  width: 100%;
  max-width: 1400px;
}

.navbar {
  position: fixed;
  display: flex;
  align-items: center;
  top: 0;
  width: 100%;
  background-color: #fff;
  height: 80px;
  border-bottom: 2px solid #000;
  z-index: 300;
}
.navbar a {
  text-decoration: none;
  color: #000;
}
.navbar__logo {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 0.5em;
}
.navbar__logo img {
  height: 60px;
}
.navbar__logo-text {
  font-size: 4rem;
  display: none;
}
.navbar__list {
  position: relative;
  display: none;
  gap: 3em;
}
.navbar__list-item {
  position: relative;
  font-size: 2.2rem;
  font-weight: 500;
}
.navbar__list-item:hover::after {
  width: 100%;
}
.navbar__list-item::after {
  transition: width 0.5s;
  position: absolute;
  content: "";
  bottom: -10px;
  left: 0;
  background-color: #6a2e94;
  height: 4px;
  width: 0;
}
.navbar__nav {
  display: flex;
  align-items: center;
  padding: 2em;
  justify-content: space-between;
}

.burger {
  display: flex;
  flex-direction: column;
  padding: 1em;
  background: transparent;
  border: none;
  outline: none;
  cursor: pointer;
}

@media (max-width: 991px) {
  .navbar__list.burger-active {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: rgb(239, 238, 238);
    position: absolute;
    top: 80px;
    left: 0;
    height: calc(100vh - 80px);
    width: 100%;
  }
  .no-scroll {
    overflow: hidden;
  }
}
.line {
  width: 3em;
  height: 0.3em;
  background-color: black;
  margin: 0.3em;
  border-radius: 20px;
  transition: 0.3s ease-in-out;
}

.header {
  position: relative;
  height: calc(75dvh - 80px);
  display: flex;
  min-height: 600px;
  margin-top: 80px;
  justify-content: center;
  align-items: center;
  background-image: url("../../dist/img/headerIMG.jpg");
  background-size: cover;
  background-position: center;
  z-index: 10;
}
.header::after {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: -1;
}
.header__box {
  width: 95%;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 3em;
  margin: 0 auto;
  color: #fff;
}
.header__box-heading {
  font-size: 2.4rem;
  font-variant: small-caps;
  font-weight: 500;
}
.header__box-heading span {
  color: #f0eb77;
}
.header__box-text {
  font-size: 1.6rem;
  font-weight: 400;
}
.header__box-button {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.4rem;
  text-decoration: none;
  color: #fff;
  background-color: #5a5a5a;
  margin: 0.5em 0;
  padding: 0.8em 1.6em;
  font-weight: 500;
  border: 2px solid transparent;
  transition: border 0.4s;
}
.header__box-button:hover {
  border: 2px solid #f0eb77;
}

.section__box {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  margin: 0 auto;
  text-align: center;
  font-weight: 600;
  padding: 4em 1em;
}
.section__box-heading {
  position: relative;
  margin: 1em 0;
  color: #000;
  font-size: 2.2rem;
  text-transform: uppercase;
}
.section__box-heading::before, .section__box-heading::after {
  content: "";
  position: absolute;
  width: 50px;
  height: 50%;
}
.section__box-heading::before {
  top: -15px;
  left: -30px;
  border-top: 4px solid #000;
  border-left: 4px solid #000;
}
.section__box-heading::after {
  bottom: -15px;
  right: -30px;
  border-bottom: 4px solid #000;
  border-right: 4px solid #000;
}
.section__box-text {
  color: #686868;
  font-size: 1.4rem;
}

.padding-section {
  padding-bottom: 6em;
}

.about__img {
  width: 100%;
  padding: 0 2em;
}
.about__img img {
  width: 100%;
  height: auto;
  max-height: 350px;
  object-fit: cover;
  object-position: center;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
}
.about__text {
  display: flex;
  justify-content: center;
  flex-direction: column;
  padding: 2em;
  font-size: 1.4rem;
  font-weight: 500;
  width: 100%;
  gap: 1.5em;
  line-height: 30px;
}
.about__text h3 {
  position: relative;
  font-size: 2rem;
  font-weight: 600;
  text-transform: uppercase;
}
.about__text h3::after {
  position: absolute;
  content: "";
  bottom: -5px;
  left: 0;
  width: 22%;
  height: 4px;
  background-color: #f0eb77;
}
.about__text p {
  font-size: 1.4rem;
  font-weight: 500;
}
.about__text--right {
  padding: 4em 2em;
}
.about__us {
  position: relative;
  overflow: hidden;
}
.about__us-box {
  width: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.main__values {
  position: relative;
  background-image: url("../../dist/img/architecture-1868667_1920.jpg");
  background-size: cover;
  background-attachment: fixed;
  background-position: center;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: #fff;
  padding: 8em 0;
  gap: 3em;
  margin: 8em 0;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
  z-index: 10;
}
.main__values::after {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.75);
  z-index: -1;
}
.main__values h3 {
  position: relative;
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  font-size: 2rem;
  margin-bottom: 2em;
  font-weight: 500;
}
.main__values h3::after {
  content: "";
  position: absolute;
  bottom: -15px;
  left: 50%;
  transform: translateX(-50%);
  width: 15%;
  height: 4px;
  background-color: #f0eb77;
}
.main__values-list {
  position: relative;
  display: flex;
  justify-content: center;
  flex-direction: column;
  padding-top: 4em;
  padding: 4em 6em;
  gap: 3em;
}
.main__values-list::after {
  position: absolute;
  content: "";
  bottom: -30px;
  left: 40px;
  width: 35%;
  height: 2px;
  background-color: #f0eb77;
}
.main__values-list--item {
  font-size: 1.6rem;
  color: #fff;
}
.main__values-list--item span {
  font-weight: 600;
  color: #f0eb77;
}

.our_mission {
  position: relative;
  flex-direction: column;
  align-items: center;
  overflow: hidden;
  z-index: 10;
}

.our__commitment {
  position: relative;
  padding: 6em 0;
  background-image: url("../../dist/img/ourExperience.jpg");
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
  z-index: 10;
}
.our__commitment::after {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: -1;
}
.our__commitment-box {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 75%;
  margin: 0 auto;
}
.our__commitment-box p {
  font-variant: small-caps;
  font-weight: 500;
  line-height: 35px;
  color: #fff;
  font-size: 1.4rem;
  text-align: center;
  text-transform: uppercase;
}
.our__commitment-button {
  margin: 2em 0;
  padding: 0.8em 1.6em;
  text-decoration: none;
  color: #fff;
  font-size: 1.4rem;
  background-color: #5a5a5a;
  font-weight: 500;
}

.services {
  position: relative;
  overflow: hidden;
}
.services__cards {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-top: 5em;
  width: 90%;
  margin: 0 auto;
  gap: 5em;
}
.services__card {
  position: relative;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  align-items: center;
  width: 85%;
  min-height: 350px;
  max-width: 300px;
  padding: 2em 2em;
  background-size: cover;
  background-position: center;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
  z-index: 10;
}
.services__card p {
  position: relative;
  font-size: 1.4rem;
  font-weight: 600;
  text-transform: uppercase;
  margin-top: 1em;
  color: #fff;
  text-align: center;
}
.services__card p::after {
  content: "";
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  height: 4px;
  width: 80%;
  background-color: #d9d9d9;
}
.services__card-button {
  text-decoration: none;
  color: #fff;
  font-size: 1.4rem;
  background-color: rgba(217, 217, 217, 0.5);
  padding: 0.5em 1em;
}
.services__card::after {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: -1;
}
.services__card--first {
  background-image: url("../../dist/img/home1.jpg");
}
.services__card--second {
  background-image: url("../../dist/img/home2.jpg");
}
.services__card--third {
  background-image: url("../../dist/img/home3.jpg");
}

.przycisk123 {
  transition: 0.5s;
}

.portfolio {
  position: relative;
  z-index: 3;
  background-color: #fdfdfd;
}
.portfolio__projects {
  width: 90%;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  flex-wrap: wrap;
  margin-top: 4em;
  gap: 6em;
}
.portfolio__project {
  position: relative;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  align-items: flex-start;
  width: 100%;
  min-height: 350px;
  background-size: cover;
  background-position: center;
  text-align: center;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
  border: 2px solid #000;
  z-index: 10;
}
.portfolio__project-text {
  width: 90%;
  margin: 0 auto;
  margin-top: 2em;
  color: #fff;
  font-size: 1.8rem;
  font-weight: bold;
  text-transform: uppercase;
}
.portfolio__project-description {
  position: relative;
  text-align: center;
  width: 100%;
  margin: 0 auto;
  margin-top: 2em;
  color: #f4f4f4;
  font-size: 1.4rem;
  margin-bottom: 2em;
  padding: 1em;
  background-color: rgba(73, 73, 73, 0.6470588235);
  border-bottom: 2px solid #000;
  border-top: 2px solid #000;
  backdrop-filter: blur(5px);
  z-index: 30;
}
.portfolio__project::after {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: -1;
}
.portfolio__project--first {
  background-image: url("../../dist/img/portfolio1.jpg");
}
.portfolio__project--second {
  background-image: url("../../dist/img/portfolio2.jpg");
}
.portfolio__project--third {
  background-image: url("../../dist/img/portfolio3.jpg");
}
.portfolio__project--fourth {
  background-image: url("../../dist/img/portfolio4.jpg");
}
.portfolio__project--fifth {
  background-image: url("../../dist/img/portfolio5.jpg");
}
.portfolio__project--sixth {
  background-image: url("../../dist/img/portfolio6.jpg");
}
.portfolio__project--seventh {
  background-image: url("../../dist/img/portfolio7.jpg");
}
.portfolio__project--eighth {
  background-image: url("../../dist/img/portfolio8.jpg");
}
.portfolio__project--ninth {
  background-image: url("../../dist/img/portfolio9.jpg");
}

.numbers {
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
  border-top: 2px solid #f4f4f4;
  padding: 3em 0;
  margin-bottom: 12em;
}

.our__scores {
  width: 85%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
}

.score {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 2em 0;
  gap: 2em;
}
.score__img {
  width: 50px;
}
.score__text {
  text-align: center;
  font-size: 1.4rem;
  font-weight: bold;
}
.score__text span {
  color: #a939d5;
}
.score__number {
  font-size: 2.8rem;
  font-weight: bold;
}

.accordion {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
  padding: 2em;
  width: 100%;
  margin: 0 auto;
  margin-bottom: 5em;
  background-color: #fff;
}
.accordion__contact {
  margin-top: 4em;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 2em;
}
.accordion__contact-text {
  font-size: 1.6rem;
  text-transform: capitalize;
  font-weight: 600;
  text-align: center;
}
.accordion__contact-button {
  text-decoration: none;
  padding: 0.5em 1em;
  color: #fff;
  font-size: 1.6rem;
  font-weight: 600;
  background-color: #383838;
}
.accordion-article {
  display: none;
  flex-direction: column;
  gap: 2em;
}
.accordion-box {
  background-color: #f7f7f7;
}
.accordion-btn {
  width: 100%;
  padding: 1em;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: none;
  text-align: left;
  border: none;
  cursor: pointer;
  font-size: 1.6rem;
}
.accordion-btn img {
  height: 40px;
}
.accordion-btn-category {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: none;
  outline: none;
  border: none;
  width: 100%;
  padding: 1em 0;
  text-align: left;
  font-size: 1.8rem;
  font-weight: 600;
  text-transform: capitalize;
  cursor: pointer;
}
.accordion-btn-category:hover span::after {
  width: 100%;
}
.accordion-btn-category span {
  position: relative;
}
.accordion-btn-category span::after {
  transition: width 1s;
  position: absolute;
  content: "";
  bottom: -10px;
  left: 0;
  width: 35%;
  height: 4px;
  background-color: #f0eb77;
}
.accordion-info {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.3s ease-in-out;
}
.accordion-info-text {
  padding: 2em;
  font-size: 1.4rem;
}

.accordion-info.active {
  max-height: 1003px;
}

.references {
  width: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 5em;
}

.slider {
  width: 100vw;
  margin: auto;
  position: relative;
}

.slides {
  user-select: none;
  display: flex;
  margin: 1em auto;
  transition: transform 0.5s ease;
}

.slider .slide:not(.active) .card3 {
  filter: blur(5px);
}

.slide {
  min-width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: transform 0.5s ease;
}

.xd2 {
  border-radius: 50%;
  width: 100%;
  height: auto;
  max-height: 150px;
  overflow: hidden;
  object-fit: cover;
}

.card3 * {
  user-select: none;
}

.xd2 img {
  width: 100%;
  height: 100%;
  max-height: 150px;
  object-fit: cover;
  object-position: center;
  border: 2px solid #000;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
}

.card3 {
  position: relative;
  text-align: center;
  width: 90%;
  max-width: 400px;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  margin: 14em auto;
  transition: filter 0.5s ease;
  cursor: grab;
}

.avatar3 {
  width: 160px;
  height: 160px;
  border-radius: 50%;
  overflow: hidden;
  margin: 0 auto 15px;
  margin-bottom: 2em;
}

.avatar3 img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.review {
  position: relative;
  font-style: italic;
  margin-bottom: 2em;
  width: 90%;
  margin: 2em auto;
  padding: 1em;
  line-height: 30px;
  font-size: 1.4rem;
}
.review::after {
  position: absolute;
  content: "“";
  font-size: 8rem;
  color: #000;
  top: 0;
  left: -30px;
}
.review::before {
  position: absolute;
  content: "”";
  font-size: 8rem;
  color: #000;
  bottom: -30px;
  right: -10px;
}

.name {
  position: relative;
  font-weight: bold;
  font-size: 1.6rem;
  margin-bottom: 1em;
}
.name::after {
  position: absolute;
  content: "";
  background-color: #f0eb77;
  bottom: -10px;
  left: 50%;
  width: 30%;
  height: 4px;
  transform: translateX(-50%);
}

.slide-content {
  display: flex;
  justify-content: center;
  text-align: center;
  flex-direction: column;
  width: 100%;
  height: 100%;
  padding: 20px;
  gap: 2em;
  position: relative;
}

.slide-content-text {
  display: flex;
  flex-direction: column;
  text-align: left;
  width: 100%;
  height: 100%;
  gap: 4em;
}
.slide-content-text h2 {
  position: relative;
  font-size: 2rem;
  font-weight: 700;
  letter-spacing: 1px;
}
.slide-content-text h2::after {
  content: "";
  position: absolute;
  width: 15%;
  height: 4px;
  background-color: #f0eb77;
  bottom: -5px;
  left: 0;
}
.slide-content-text p {
  font-size: 1.5rem;
}

.arrow {
  background: transparent;
  border: none;
  color: black;
  padding: 10px;
  cursor: pointer;
  z-index: 100;
  font-size: 6rem;
}

.arrow-prev {
  left: 0;
}

.arrow-next {
  right: 0;
}

.dots {
  width: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-top: 20px;
  margin: 2em auto 0;
}

.dot {
  height: 10px;
  width: 10px;
  background-color: #bbb;
  border-radius: 50%;
  display: inline-block;
  margin: 0 5px;
  cursor: pointer;
}

.reference__bottom {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 60%;
  margin: 0 auto 5em;
}

.dot.active {
  background-color: #717171;
}

.forms {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.xd {
  width: 30px;
}

.contact__input {
  position: relative;
  width: 100%;
  border: none;
  outline: none;
  padding: 0.5em 0;
  font-size: 1.6rem;
  background-color: #fff;
  z-index: 30;
  padding-left: 5px;
}
.contact__box {
  position: relative;
  display: flex;
  flex-direction: row;
  margin: 2em 0;
  border-bottom: 2px solid #000;
  z-index: 30;
}
.contact__box:last-of-type {
  flex-direction: column;
  border-bottom: none;
  align-items: center;
}
.contact__box:last-of-type span {
  display: flex;
  align-items: center;
  font-size: 1.6rem;
  color: #494949;
  margin: 1em 0;
}
.contact__box:last-of-type span img {
  margin-right: 0.5em;
}
.contact__textarea {
  width: 100%;
  resize: vertical;
  min-height: 250px;
  max-height: 400px;
  border: 2px solid #000;
  padding: 2em;
  outline: none;
}
.contact__btn {
  border: none;
  outline: none;
  width: 100%;
  padding: 0.8em 1.6em;
  background-color: #323232;
  font-size: 1.6rem;
  color: #fff;
  font-family: Montserrat;
  text-transform: uppercase;
  font-variant: small-caps;
  font-weight: bold;
  cursor: pointer;
}
.contact__form {
  display: flex;
  flex-direction: column-reverse;
  justify-content: center;
  padding: 2em;
