.fade-in {
  opacity: 0;
  transition: opacity 0.5s ease-in;
}

@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.shapes__container {
  position: relative;
  overflow: hidden;
  z-index: 30;
}

@media (min-width: 1400px) {
  .shapes__container {
    overflow: visible;
  }
  .accordion {
    z-index: 30;
  }
}
.shape {
  position: absolute;
  width: 50px;
  height: 50px;
  border: 2px solid #000;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
  z-index: -11;
}

.square__first {
  left: -30px;
  top: -20px;
  transform: rotate(45deg);
  background-color: #323232;
}

.square__second {
  top: -30px;
  left: -20px;
  background-color: #f0eb77;
  transform: rotate(45deg);
  border: none;
  z-index: 3;
}

.square__third {
  top: -100px;
  right: -30px;
  transform: rotate(45deg);
  z-index: 3;
}

.square__fourth {
  top: 0;
  right: -50px;
}

.circle {
  background-color: #323232;
  border: none;
  top: 850px;
  left: 20%;
  border-radius: 50%;
}

.square__first {
  animation: rotate 20s linear infinite;
}

.square__xd1 {
  top: 120px;
  left: -35px;
  transform: rotate(45deg);
}

.square__xd {
  top: 500px;
  right: -45px;
  background-color: #f0eb77;
  transform: rotate(45deg);
  z-index: 5;
}

.square__xd3 {
  bottom: -20px;
  left: -30px;
  transform: rotate(45deg);
  animation: rotate 20s linear infinite;
  z-index: 1;
}

.rotate {
  transform: rotate(180deg);
}